import { mapActions, mapGetters } from 'vuex';

export default {
    name: "eventBus",
    methods: {
        ...mapActions({'loadSnippets': 'codeSnippet/loadEventBusSnippets'}),
    },
    computed: {
        ...mapGetters({'snippets': 'codeSnippet/getEventBusSnippets'})
    },
    created () {
        this.loadSnippets();
    },
};
